import React from "react";
import wrapper from "../../../commons/WebComponentWrapper";
import MobileStepper from "@mui/material/MobileStepper";

const dispatchedEvents = [];

const SrsMuiMobileStepper = function (props, ref) {
  return (
    <MobileStepper
      variant={props.variant}
      steps={props.steps}
      position={props.position}
      activeStep={props.activeStep}
      nextButton={props.nextButton}
      backButton={props.backButton}
      LinearProgressProps={props.LinearProgressProps}
      theme={props.muiTheme}
    />
  );
};

const styles = `
  /* Custom styles for the MuiMobileStepper component */
`;

const props = [
  {
    name: "variant",
    defaultValue: "dots",
    type: "string",
  },
  {
    name: "steps",
    defaultValue: 0,
    type: "number",
  },
  {
    name: "position",
    defaultValue: "bottom",
    type: "string",
  },
  {
    name: "activeStep",
    defaultValue: 0,
    type: "number",
  },
  {
    name: "nextButton",
    defaultValue: null,
    type: "string",
  },
  {
    name: "backButton",
    defaultValue: null,
    type: "string",
  },
  {
    name: "LinearProgressProps",
    defaultValue: {},
    type: "object",
  },
  {
    name: "sx",
    defaultValue: {},
    type: "object"
  }
];

const slots = [
  {
    name: "default",
    allowed: [],
  },
];

const WrappedComponent = wrapper(
  React.forwardRef(SrsMuiMobileStepper),
  props,
  "mui"
);
export { WrappedComponent, styles, props, dispatchedEvents, slots };
